<script>

import FveMixinFieldVueMultiselect from '@fve/Element/Select/FveMixinFieldVueMultiselect';

export default {
  mixins: [
    FveMixinFieldVueMultiselect
  ],
  props: {
    //    (  )
    value    : { type: Array, default: () => [] },
  },
  data(){
    return {
      multiple: true,
      search: '',
      searchAdd: false,
    };
  },
  methods: {
    addItem(){},

    //   ->      DateTime
    prepareInput(value){
      return value.map( (item) => {
        return {
          id: this.optionGetKey(item),
          name: this.optionGetName(item),
          origin: item,
        };
      });
    },
    //       DateTime
    prepareOutput(value){
      return value.map( (item) => {
        return item.origin;
      });
    },
  },
};
</script>
